















































import { Vue, Component, Prop, Mixins, Emit } from "vue-property-decorator";
import { isURL } from "validator";
import { LocaleMessages } from "vue-i18n";
import { repositoriesModule } from "@/store/modules";
import { Overwrite } from "@/shared/types";
import { IRepository } from "@/store/modules/RepositoriesModule";

@Component({
  components: {}
})
export default class RepositoryEditorComponent extends Mixins() {
  maxNameLength = 100;
  maxPasswordLength = 100;
  valid = true;
  name = "";
  password = "";
  location = "";
  s3AccessKey = "";
  s3SecretKey = "";

  async submit() {
    if ((this.$refs.form as any).validate()) {
      this.formSubmit({
        name: this.name,
        password: this.password,
        location: this.location,
        s3AccessKey: this.s3AccessKey,
        s3SecretKey: this.s3SecretKey
      });
    }
  }

  @Emit("form-submit")
  formSubmit(
    payload: Overwrite<
      IRepository,
      {
        id?: number;
      }
    >
  ) {}

  clear() {
    (this.$refs.form as any).reset(); // TODO: fix types
  }

  get validateRules(): {
    [idx: string]: ((val: string) => boolean | string | LocaleMessages)[];
  } {
    // regex for keys from https://aws.amazon.com/blogs/security/a-safer-way-to-distribute-aws-credentials-to-ec2/
    return {
      s3AccessKey: [
        v => !!v || this.$t("repository-editor.s3-access-key-required"),
        v =>
          (v && /^([A-Z0-9]{20})$/g.test(v)) ||
          this.$t("repository-editor.s3-access-key-invalid")
      ],
      s3SecretKey: [
        v => !!v || this.$t("repository-editor.s3-secret-key-required"),
        v =>
          (v && /^([A-Za-z0-9/+=]{40})$/g.test(v)) ||
          this.$t("repository-editor.s3-secret-key-invalid")
      ],
      location: [
        v => !!v || this.$t("repository-editor.location-required"),
        v => (v && isURL(v)) || this.$t("repository-editor.invalid-url")
      ],
      password: [
        v => !!v || this.$t("repository-editor.password-required"),
        v =>
          (v && v.length <= this.maxPasswordLength) ||
          this.$t("repository-editor.password-length", {
            length: this.maxPasswordLength
          })
      ],
      name: [
        v => !!v || this.$t("repository-editor.name-required"),
        v =>
          (v && v.length <= this.maxNameLength) ||
          this.$t("repository-editor.name-length", {
            length: this.maxPasswordLength
          })
      ]
    };
  }
}
